import { AppBar, Toolbar, Box, Button, SvgIcon } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { ReactComponent as MenuIcon } from "../../assets/icons/hamburger.svg";
import { ReactComponent as OlympusIcon } from "../../assets/icons/TAZOR_logo.svg";
import OhmMenu from "./OhmMenu.jsx";
import TazorPrice from "./TazorPrice.jsx";
import TazPrice from "./TazPrice.jsx";
import ThemeSwitcher from "./ThemeSwitch.jsx";
import LocaleSwitcher from "./LocaleSwitch.tsx";
import ConnectMenu from "./ConnectMenu.jsx";
import "./topbar.scss";
import NetworkMenu from "./NetworkMenu.jsx";

const useStyles = makeStyles(theme => ({
  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: "100%",
      padding: "10px",
    },
    justifyContent: "flex-end",
    alignItems: "flex-end",
    background: "transparent",
    backdropFilter: "none",
    zIndex: 10,
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("981")]: {
      display: "none",
    },
  },
}));

function TopBar({ theme, toggleTheme, handleDrawerToggle }) {
  const classes = useStyles();
  const isVerySmallScreen = useMediaQuery("(max-width: 355px)");
  const isSmallScreen = useMediaQuery("(max-width: 500px)");

  return (
    <AppBar position="sticky" className={classes.appBar} elevation={0}>
      <Toolbar disableGutters className="dapp-topbar">
        {/* this is tarzan code */}
        <Box display="flex" className="mobilelogo">
          <SvgIcon
            color="primary"
            component={OlympusIcon}
            viewBox="0 0 200 60"
            style={{ minWdth: "70px", minHeight: "40px", width: "170px" }}
          />
        </Box>
        <Box display="flex">
          <Button
            id="hamburger"
            aria-label="open drawer"
            edge="start"
            size="large"
            variant="contained"
            color="secondary"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <SvgIcon component={MenuIcon} />
          </Button>

          {!isSmallScreen && <TazorPrice />}
          {!isSmallScreen && <TazPrice />}
          {!isVerySmallScreen && <OhmMenu />}

          {/* <NetworkMenu /> */}

          <ConnectMenu theme={theme} />

          {/* <ThemeSwitcher theme={theme} toggleTheme={toggleTheme} /> */}

          {/* <LocaleSwitcher theme={theme} /> */}
        </Box>
      </Toolbar>
    </AppBar>
  );
}

export default TopBar;
